import React, { Component } from 'react'
import { Routes, Route } from 'react-router';

import Home from '../Pages/Home';
import About from '../Pages/About';
import Skills from '../Pages/Skills';
import Lemverify from '../Pages/Projects/Lemverify';
import Biz2click from '../Pages/Projects/Biz2click';

import './index.css';

class Body extends Component {
    render() {
        return (
            <div className='main-body'>
                <Routes>
                    <Route exact path='/myportfolio' element={<Home />} />
                    <Route path='/myportfolio/about' element={<About />} />
                    <Route path='/myportfolio/skills' element={<Skills />} />
                    <Route path='/myportfolio/projects/lemverify' element={<Lemverify />} />
                    <Route path='/myportfolio/projects/biz2click' element={<Biz2click />} />
                </Routes>
            </div>
        )
    }
}

export default Body;